html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  .name,
  .number{
  display: inline-block;
  font-family: sans-serif;
  font-size: 16px;
  margin-bottom: 8px;
  font-weight: 400;
  }

  .name {
      margin-right: 10px;
  }

  .buttonForm {
    margin-top: 10px;
    border-color: rgb(46, 45, 45);
    font-family: sans-serif;
    font-size: 14px;
    padding: 5px;
    border-radius: 4px;
    margin-left: 15px;
  }
